import CitySelector from './city-selector'

const install = function (Vue) {
  // 
  if (install.installed) return
  // 
  Vue.component(CitySelector.name, CitySelector)
}

if (typeof window !== 'undefined' && window.Vue) {
  install(window.Vue)
}

export default {
  //  install Vue.use() 
  install,
  // 
  CitySelector
}
